.App {
  text-align: center;

}
.Form {
  background-color: rgb(87, 4, 165);
  border-radius: 20px;
  padding: 50px;
  margin-top: 20px !important;
  margin: auto;
  max-width: 60%;
  height: 100%;
  background-size: cover;
  -webkit-box-shadow: 3px 5px 3px rgb(182, 177, 177);
          box-shadow: 3px 5px 3px rgb(182, 177, 177);
}
.Input{
  border: 0;
  background-color:  rgb(87, 4, 165);
  border-bottom:3px solid rgb(255, 255, 255) ;
  outline: none;
  color: #fff;
  width: 80%;
  padding: 20px;
}
.Button{
  border: 3px solid rgb(255, 255, 255);
  background-color:  rgb(87, 4, 165);
  outline: none;
  color: #fff;
  padding: 20px;
  border-radius: 10px;
}


