.List{
    background-color: orange;
    border-radius: 20px;
    padding: 20px;
    margin: auto;
    max-width: 50%;
    height: 100%;
    background-size: cover;
    -webkit-box-shadow: 3px 5px 3px rgb(182, 177, 177);
            box-shadow: 3px 5px 3px rgb(182, 177, 177);
    list-style: none;
    -ms-flex-align: end;
        align-items: flex-end;
    margin-top: 10px;
}

.Out {
    border-radius: 20px;
    padding: 20px;
    margin: auto;
    width: 50%;
    height: 100%;
    background-size: cover;
    -webkit-box-shadow: 3px 5px 3px rgb(182, 177, 177);
            box-shadow: 3px 5px 3px rgb(182, 177, 177);
    list-style: none;
    -ms-flex-align: end;
        align-items: flex-end;
    margin-top: 10px;    
    cursor: pointer;
    -webkit-animation-name: example;
            animation-name: example;
    -webkit-animation-duration: 4s;
            animation-duration: 4s;
    background: red;
    -webkit-animation-timing-function:linear;
            animation-timing-function:linear;
    -webkit-animation-fill-mode: forwards;
            animation-fill-mode: forwards;
    -webkit-animation-direction: alternate;
            animation-direction: alternate;
}
@-webkit-keyframes example {
    from {background-color: orange;}
    to {background-color: red;}
  }
@keyframes example {
    from {background-color: orange;}
    to {background-color: red;}
  }
.List li,.Out li{
    position: relative;
}
.List li .Trash, .Out li  .Trash{
    position: absolute;
    right: 20px;
}
.Trash{
    color:white;
  
}
.Trash:hover{
    color:white;
    font-size: 20px;
}

.List li .Tick, .Out li  .Tick{
    position: absolute;
   left: 20px;
}
.Tick{
    color:white;
  
}
.Tick:hover{
    color:white;
    font-size: 20px;
}
.In {
    border-radius: 20px;
    padding: 20px;
    margin: auto;
    width: 50%;
    height: 100%;
    background-size: cover;
    -webkit-box-shadow: 3px 5px 3px rgb(182, 177, 177);
            box-shadow: 3px 5px 3px rgb(182, 177, 177);
    list-style: none;
    -ms-flex-align: end;
        align-items: flex-end;
    margin-top: 10px;    
    cursor: pointer;
    -webkit-animation-name: exampl;
            animation-name: exampl;
    -webkit-animation-duration: 4s;
            animation-duration: 4s;
    background: greenyellow;
    -webkit-animation-timing-function:linear;
            animation-timing-function:linear;
    -webkit-animation-fill-mode: forwards;
            animation-fill-mode: forwards;
    -webkit-animation-direction: alternate;
            animation-direction: alternate;
    text-decoration: line-through;
}
@-webkit-keyframes exampl {
    from {background-color: orange;}
    to {background-color: greenyellow;}
  }
@keyframes exampl {
    from {background-color: orange;}
    to {background-color: greenyellow;}
  }


